.todoblock {
  width: 500px;
  background: white;
  border: 1px solid lightgreen;
  border-radius: 10px;
  margin: 0 auto;
  overflow: hidden;
  font-family: "Over Pass", "Times New Roman", serif;
}

.todoblock .block-title {
  background: green;
  color: white;
  margin: 0;
  padding: 10px 20px;
}

.todoblock .block-content {
  background: white;
  color: green;
  margin: 0;
  font-size: 18px;
  padding: 20px 10px;
}

.todoblock .todo-box-lists {
  padding: 10px 0;
}

.todoblock .clear-all-text {
  color: #dc9433;
  font-size: 18px;
  float: right;
  padding: 5px 0px;
  font-weight: bold;
  cursor: pointer;
  transform: scale(1);
  transition: 0.2s all cubic-bezier(0, 0, 0, 0.4);
}

.todoblock .clear-all-text:hover {
  transform: scale(1.1);
  text-decoration: underline;
}
